body {
  background-color: rgb(39, 39, 39);
  color: white;
  height: 100vh;
  margin: 0;
}

.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(43, 42, 42);
  height: 5vh;
}

.header div {
  font-size: 20px;
  font-weight: bold;
}

.text-box-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f8f8f8;
}

.kategoria {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.menu-item {
  width: 300px;
  margin: 20px;
  padding: 15px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }

  h1,
  h2 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }

  img {
    width: 100%;
    border-radius: 8px;
    margin-bottom: 10px;
  }

  button {
    background-color: #4caf50;
    color: #fff;
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;

    &:hover {
      background-color: #45a049;
    }
  }
}

.chef-section {
  margin-top: 30px;
  text-align: center;
  padding: 20px;
  background-color: #f8f8f8;
  border-radius: 10px;
}

.chef-image {
  width: 50%;
  height: auto;
  max-height: 50vh;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.chef-section h2 {
  font-size: 1.5em;
  color: #333;
  font-family: 'Poppins', sans-serif;
  margin-bottom: 20px;
}

.chef-section p {
  font-size: 1em;
  color: #666;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
}

@media only screen and (max-width: 800px) {
  .menu-item {
    width: 100%; /* Adjust the width for smaller screens */
    max-width: 300px; /* Limit the maximum width */
  }

  .chef-section {
    width: 80%; /* Adjust the width for smaller screens */
  }
}
